* {
  box-sizing: border-box;
  letter-spacing: -0.5px;
}

#root,
body{
  min-height: 100vh;
  --tw-bg-opacity: 1;
  background-color: rgb(0 0 0 / var(--tw-bg-opacity));

  letter-spacing: -0.5px;
}

/*** HIDE NUMBER INPUT STEPS BUTTONS ****/
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/* Firefox */
input[type='number'] {
  -moz-appearance: textfield;
}

button,
input {
  outline: none !important;
}

/*react error overlay hidden in dev mode while testing network switches which trow errors*/
body > iframe {
  display: none !important;
}

/* Toast styles */
.Toastify__toast-body {
  padding: 0;
  padding-bottom: 4px;
}

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 32px;
  height: 16px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: '';
  height: 10px;
  width: 10px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  transition: 0.4s;
}

.web3modal-modal-lightbox {
  z-index: 50 !important;
}

input:checked + .slider {
  background-color: #1a1c1d;
}

input:focus + .slider {
  box-shadow: 0 0 1px #1a1c1d;
}

input:checked + .slider:before {
  transform: translateX(16px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

/* Tippy themes */
.tippy-box[data-theme='orange'] {
  background-color: #ffeed3;
  color: #ffeed3;
}

.tippy-box[data-theme='orange'] > .tippy-arrow {
  color: inherit;
}

.tippy-box[data-theme='orange'] > .tippy-content {
  color: #60461f;
}

